'use client';

import { useEffect, useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { useAuth } from '@/context/AuthContext';

interface Testimonial {
  _id: string;
  patientId: string;
  patientName: string;
  rating: number;
  feedback: string;
  treatmentType: string;
  isPublished: boolean;
  createdAt: string;
  recordedBy?: {
    name: string;
    email: string;
  };
  doctor?: {
    name: string;
    email: string;
  };
}

export default function TestimonialsPage() {
  const { user } = useAuth();
  const [testimonials, setTestimonials] = useState<Testimonial[]>([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState<'all' | 'published'>('all');
  const [viewMode, setViewMode] = useState<'list' | 'grouped'>('list');

  useEffect(() => {
    loadTestimonials();
  }, [filter]);

  const loadTestimonials = async () => {
    try {
      setLoading(true);
      const queryParam = filter === 'published' ? '?published=true' : '';
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients/testimonials/all${queryParam}`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        }
      });

      if (response.ok) {
        const data = await response.json();
        setTestimonials(data);
      } else {
        console.error('Failed to load testimonials');
      }
    } catch (error) {
      console.error('Load testimonials error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleTogglePublish = async (patientId: string, testimonialId: string, currentStatus: boolean) => {
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients/${patientId}/testimonial/${testimonialId}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ isPublished: !currentStatus })
      });

      if (response.ok) {
        loadTestimonials();
      }
    } catch (error) {
      console.error('Toggle publish error:', error);
    }
  };

  const getAverageRating = () => {
    if (testimonials.length === 0) return 0;
    const sum = testimonials.reduce((acc, t) => acc + t.rating, 0);
    return (sum / testimonials.length).toFixed(1);
  };

  const getRatingDistribution = () => {
    const distribution = { 5: 0, 4: 0, 3: 0, 2: 0, 1: 0 };
    testimonials.forEach(t => {
      distribution[t.rating as keyof typeof distribution]++;
    });
    return distribution;
  };

  const getGroupedByDoctor = () => {
    const grouped: { [key: string]: { doctor: { name: string; email: string } | undefined; testimonials: Testimonial[] } } = {};
    
    testimonials.forEach(testimonial => {
      const doctorKey = testimonial.doctor?.name || 'Unassigned';
      if (!grouped[doctorKey]) {
        grouped[doctorKey] = {
          doctor: testimonial.doctor,
          testimonials: []
        };
      }
      grouped[doctorKey].testimonials.push(testimonial);
    });
    
    return grouped;
  };

  const getDoctorStats = (doctorTestimonials: Testimonial[]) => {
    const avgRating = doctorTestimonials.length > 0
      ? (doctorTestimonials.reduce((acc, t) => acc + t.rating, 0) / doctorTestimonials.length).toFixed(1)
      : '0';
    const publishedCount = doctorTestimonials.filter(t => t.isPublished).length;
    return { avgRating, publishedCount, total: doctorTestimonials.length };
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-amber-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading testimonials...</p>
        </div>
      </div>
    );
  }

  const distribution = getRatingDistribution();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Patient Testimonials</h1>
          <p className="text-gray-600 mt-1">View and manage patient feedback</p>
        </div>
        <div className="flex gap-4">
          {/* View Mode Toggle */}
          <div className="flex gap-2 border-r border-gray-300 pr-4">
            <button
              onClick={() => setViewMode('list')}
              className={`px-4 py-2 rounded-lg font-medium transition-all flex items-center gap-2 ${
                viewMode === 'list'
                  ? 'bg-gradient-to-r from-purple-500 to-pink-500 text-white shadow-md'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
              List
            </button>
            {(user?.role === 'admin' || user?.role === 'subadmin') && (
              <button
                onClick={() => setViewMode('grouped')}
                className={`px-4 py-2 rounded-lg font-medium transition-all flex items-center gap-2 ${
                  viewMode === 'grouped'
                    ? 'bg-gradient-to-r from-purple-500 to-pink-500 text-white shadow-md'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
                By Doctor
              </button>
            )}
          </div>

          {/* Filter Toggle */}
          <div className="flex gap-2">
            <button
              onClick={() => setFilter('all')}
              className={`px-4 py-2 rounded-lg font-medium transition-all ${
                filter === 'all'
                  ? 'bg-gradient-to-r from-amber-500 to-orange-500 text-white shadow-md'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              All ({testimonials.length})
            </button>
            <button
              onClick={() => setFilter('published')}
              className={`px-4 py-2 rounded-lg font-medium transition-all ${
                filter === 'published'
                  ? 'bg-gradient-to-r from-amber-500 to-orange-500 text-white shadow-md'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              Published
            </button>
          </div>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="shadow-lg border-gray-200 bg-gradient-to-br from-amber-50 to-orange-50">
          <CardContent className="p-6">
            <div className="flex items-center gap-4">
              <div className="p-3 bg-amber-500 rounded-full">
                <svg className="w-8 h-8 text-white" fill="currentColor" viewBox="0 0 20 20">
                  <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                </svg>
              </div>
              <div>
                <p className="text-sm text-gray-600">Average Rating</p>
                <p className="text-3xl font-bold text-gray-900">{getAverageRating()}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="shadow-lg border-gray-200 bg-gradient-to-br from-blue-50 to-indigo-50">
          <CardContent className="p-6">
            <div className="flex items-center gap-4">
              <div className="p-3 bg-blue-500 rounded-full">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
              </div>
              <div>
                <p className="text-sm text-gray-600">Total Testimonials</p>
                <p className="text-3xl font-bold text-gray-900">{testimonials.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="shadow-lg border-gray-200 bg-gradient-to-br from-green-50 to-teal-50">
          <CardContent className="p-6">
            <div className="flex items-center gap-4">
              <div className="p-3 bg-green-500 rounded-full">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
              <div>
                <p className="text-sm text-gray-600">Published</p>
                <p className="text-3xl font-bold text-gray-900">
                  {testimonials.filter(t => t.isPublished).length}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Rating Distribution */}
      <Card className="shadow-lg border-gray-200">
        <CardHeader>
          <CardTitle>Rating Distribution</CardTitle>
        </CardHeader>
        <CardContent className="p-6">
          <div className="space-y-3">
            {[5, 4, 3, 2, 1].map((rating) => (
              <div key={rating} className="flex items-center gap-3">
                <span className="text-sm font-medium text-gray-700 w-8">{rating}★</span>
                <div className="flex-1 bg-gray-200 rounded-full h-3 overflow-hidden">
                  <div
                    className="bg-gradient-to-r from-amber-500 to-orange-500 h-full rounded-full transition-all"
                    style={{
                      width: testimonials.length > 0
                        ? `${(distribution[rating as keyof typeof distribution] / testimonials.length) * 100}%`
                        : '0%'
                    }}
                  />
                </div>
                <span className="text-sm text-gray-600 w-12 text-right">
                  {distribution[rating as keyof typeof distribution]}
                </span>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Testimonials - List or Grouped View */}
      {viewMode === 'list' ? (
        /* List View */
        <Card className="shadow-lg border-gray-200">
          <CardContent className="p-6">
            {testimonials.length > 0 ? (
              <div className="space-y-4">
                {testimonials.map((testimonial) => (
                  <div
                    key={testimonial._id}
                    className="p-6 bg-white border border-gray-200 rounded-lg shadow-sm hover:shadow-md transition-shadow"
                  >
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <div className="flex items-center gap-3 mb-3">
                          <h3 className="text-lg font-semibold text-gray-900">
                            {testimonial.patientName}
                          </h3>
                          <span className="text-xs font-medium px-2 py-1 bg-amber-100 text-amber-700 rounded-full">
                            {testimonial.treatmentType.replace('_', ' ')}
                          </span>
                          {testimonial.isPublished ? (
                            <span className="text-xs font-medium px-2 py-1 bg-green-100 text-green-700 rounded-full">
                              Published
                            </span>
                          ) : (
                            <span className="text-xs font-medium px-2 py-1 bg-gray-100 text-gray-700 rounded-full">
                              Draft
                            </span>
                          )}
                        </div>

                        <div className="flex items-center gap-1 mb-3">
                          {[1, 2, 3, 4, 5].map((star) => (
                            <svg
                              key={star}
                              className={`w-5 h-5 ${
                                star <= testimonial.rating ? 'text-amber-500' : 'text-gray-300'
                              }`}
                              fill="currentColor"
                              viewBox="0 0 20 20"
                            >
                              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                            </svg>
                          ))}
                        </div>

                        <p className="text-gray-700 leading-relaxed mb-3">{testimonial.feedback}</p>

                        <div className="flex items-center gap-4">
                          <p className="text-xs text-gray-500">
                            {new Date(testimonial.createdAt).toLocaleDateString('en-US', {
                              year: 'numeric',
                              month: 'long',
                              day: 'numeric'
                            })}
                          </p>
                          {testimonial.recordedBy && (
                            <p className="text-xs text-gray-500">
                              • Doctor: {testimonial.recordedBy.name}
                            </p>
                          )}
                        </div>
                      </div>

                      <button
                        onClick={() => handleTogglePublish(testimonial.patientId, testimonial._id, testimonial.isPublished)}
                        className={`ml-4 px-4 py-2 rounded-lg font-medium transition-all ${
                          testimonial.isPublished
                            ? 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                            : 'bg-gradient-to-r from-green-500 to-teal-500 text-white hover:from-green-600 hover:to-teal-600'
                        }`}
                      >
                        {testimonial.isPublished ? 'Unpublish' : 'Publish'}
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-12 text-gray-500">
                <svg
                  className="w-16 h-16 text-gray-400 mx-auto mb-4"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z"
                  />
                </svg>
                <p className="text-lg font-medium">No testimonials found</p>
                <p className="text-sm mt-1">
                  {filter === 'published'
                    ? 'No published testimonials yet'
                    : 'Start collecting patient feedback from the patient detail pages'}
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      ) : (
        /* Grouped by Doctor View */
        <div className="space-y-6">
          {testimonials.length > 0 ? (
            Object.entries(getGroupedByDoctor()).map(([doctorName, data]) => {
              const stats = getDoctorStats(data.testimonials);
              return (
                <Card key={doctorName} className="shadow-lg border-gray-200 bg-gradient-to-br from-purple-50 to-pink-50">
                  <CardHeader className="border-b border-purple-100/50">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-4">
                        <div className="p-3 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full">
                          <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                          </svg>
                        </div>
                        <div>
                          <h3 className="text-xl font-bold text-gray-900">
                            {doctorName}
                          </h3>
                          {data.doctor?.email && (
                            <p className="text-sm text-gray-600">{data.doctor.email}</p>
                          )}
                        </div>
                      </div>
                      
                      <div className="flex items-center gap-6">
                        <div className="text-center">
                          <div className="flex items-center gap-1">
                            <svg className="w-5 h-5 text-amber-500" fill="currentColor" viewBox="0 0 20 20">
                              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                            </svg>
                            <span className="text-2xl font-bold text-gray-900">{stats.avgRating}</span>
                          </div>
                          <p className="text-xs text-gray-600">Avg Rating</p>
                        </div>
                        <div className="text-center border-l border-gray-300 pl-6">
                          <p className="text-2xl font-bold text-gray-900">{stats.publishedCount}/{stats.total}</p>
                          <p className="text-xs text-gray-600">Published</p>
                        </div>
                      </div>
                    </div>
                  </CardHeader>
                  
                  <CardContent className="p-6">
                    <div className="space-y-4">
                      {data.testimonials.map((testimonial) => (
                        <div
                          key={testimonial._id}
                          className="p-5 bg-white border border-gray-200 rounded-lg shadow-sm hover:shadow-md transition-shadow"
                        >
                          <div className="flex items-start justify-between">
                            <div className="flex-1">
                              <div className="flex items-center gap-3 mb-2">
                                <h4 className="text-lg font-semibold text-gray-900">
                                  {testimonial.patientName}
                                </h4>
                                <span className="text-xs font-medium px-2 py-1 bg-amber-100 text-amber-700 rounded-full">
                                  {testimonial.treatmentType.replace('_', ' ')}
                                </span>
                                {testimonial.isPublished ? (
                                  <span className="text-xs font-medium px-2 py-1 bg-green-100 text-green-700 rounded-full">
                                    Published
                                  </span>
                                ) : (
                                  <span className="text-xs font-medium px-2 py-1 bg-gray-100 text-gray-700 rounded-full">
                                    Draft
                                  </span>
                                )}
                              </div>

                              <div className="flex items-center gap-1 mb-2">
                                {[1, 2, 3, 4, 5].map((star) => (
                                  <svg
                                    key={star}
                                    className={`w-4 h-4 ${
                                      star <= testimonial.rating ? 'text-amber-500' : 'text-gray-300'
                                    }`}
                                    fill="currentColor"
                                    viewBox="0 0 20 20"
                                  >
                                    <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                                  </svg>
                                ))}
                              </div>

                              <p className="text-gray-700 leading-relaxed mb-2">{testimonial.feedback}</p>

                              <p className="text-xs text-gray-500">
                                {new Date(testimonial.createdAt).toLocaleDateString('en-US', {
                                  year: 'numeric',
                                  month: 'long',
                                  day: 'numeric'
                                })}
                              </p>
                            </div>

                            <button
                              onClick={() => handleTogglePublish(testimonial.patientId, testimonial._id, testimonial.isPublished)}
                              className={`ml-4 px-4 py-2 rounded-lg font-medium transition-all ${
                                testimonial.isPublished
                                  ? 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                  : 'bg-gradient-to-r from-green-500 to-teal-500 text-white hover:from-green-600 hover:to-teal-600'
                              }`}
                            >
                              {testimonial.isPublished ? 'Unpublish' : 'Publish'}
                            </button>
                          </div>
                        </div>
                      ))}
                    </div>
                  </CardContent>
                </Card>
              );
            })
          ) : (
            <Card className="shadow-lg border-gray-200">
              <CardContent className="p-6">
                <div className="text-center py-12 text-gray-500">
                  <svg
                    className="w-16 h-16 text-gray-400 mx-auto mb-4"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z"
                    />
                  </svg>
                  <p className="text-lg font-medium">No testimonials found</p>
                  <p className="text-sm mt-1">
                    {filter === 'published'
                      ? 'No published testimonials yet'
                      : 'Start collecting patient feedback from the patient detail pages'}
                  </p>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      )}
    </div>
  );
}
